import logo from './logo.svg';
import './App.css';
import { Plugins } from '@capacitor/core';
import { useState } from 'react';

const { Device, Modals, Browser } = Plugins;
function MyApp() {
  const [showApp, setShowApp] = useState(false);
  const url = 'https://app.eventmobi.com/core/';

  // useEffect(() => {
  //   Browser.prefetch({
  //     urls: [
  //       url
  //     ]
  //   });
  // }, []);
  const showDeviceInfo = async () => {
    const info = await Device.getInfo();
    await Modals.alert({
      title: 'Info',
      message: `UUID: ${info.uuid};
        Model: ${info.model}`
    });
  }
  const openInBrowserApp = async () => {
    setShowApp(true);
    // await Browser.open({
    //   url,
    // });
  }

  return (
    showApp ?
      <iframe
        title="Event Mobi POC"
        style={{
          display: 'block',
          width: '100%',
          margin: '0px',
          padding: '0px',
          height: '100%',
          border: 'none',
        }}
        frameborder="0"
        marginheight="0"
        marginwidth="0"
        width="100%"
        height="100%"
        scrolling="auto"

        src={url} />
      : <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Served version
        </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
        </a>
          <button onClick={showDeviceInfo}>Show Device Info</button>
          <button onClick={openInBrowserApp}>Open In-Brower App</button>
        </header>
      </div>
  );
}

export default MyApp;
